const MAX_DELTA = 0.25;

export function filterSearchResults(results) {
  var score1 = 0;
  var score2 = 0;
  for (var i = 0; i < results.length - 1; i++) {
    score1 = results[i].score;
    score2 = results[i + 1].score;
    let delta = Math.abs(score1 - score2);

    if (delta > MAX_DELTA) {
      results.splice(i + 1);
      i--;
    }
  }

  return results;
}
