import React from 'react';
import './App.css';
import { filterSearchResults } from './search'
const laws = require('./search/laws.json')
const Fuse = require('fuse.js')

const options = {
  includeScore: true,
  isCaseSensitive: false,
  threshold: 0.5,
  keys: ['Desc'],
}

const fuse = new Fuse(laws, options)

class App extends React.Component {

  state = {
    text: '',
    results: [],
  }

  handleChange(event) {
    this.setState({ text: event.target.value });
  }

  search() {
    const results = fuse.search('passenger helmet')
    this.setState({ results: filterSearchResults(results) })
  }

  renderResults() {
    return this.state.results.map((r, i) => {
      return <div>
        <p>{r.Chapter + ':' + r.Section}</p>
        <h3>{r.Desc}</h3>
        <p>1st Off: {r["1ST Of."]}</p>
        <p>2nd Off: {r["2ND Of."]}</p>
        <p>3rd Off: {r["3RD Of."]}</p>
      </div>
    })
  }

  render() {
    return (
      <div>
        <div>
          <label>
            Query Text:
            <input type="text" value={this.state.text} onChange={this.handleChange.bind(this)} />
          </label>
          <button onClick={this.search.bind(this)}>
            Search
          </button>
        </div>
        <div>
          {this.renderResults()}
        </div>
      </div>
    );
  }
}

export default App;
